import React from "react";

export const Footer = () => {
    let footerStyle = {
        width:"100%",
        position:"relative",
        top:"100px",
    }
  return (
    <footer style={footerStyle} className="bg-dark text-light text-center py-3 mt-5">
      &copy;Copyright 2021 All Right are not Reserved by Us.
    </footer>
  );
};


